.noteViewBox {
  display: flex;
  justify-content: stretch;
  flex-grow: 2;
  background-color: #e9c46a;
  border-radius: 15px;
}

.noteBox {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 100%;
}

.noteList {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 100%;
  padding: 20px 5px 20px 5px;
  margin: 10px 0px 10px 0px;
}

.note {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 80%;
  text-decoration: none;
  background-color: #2a9d8f;
  color: white;
  border: solid 2px black;
  border-radius: 15px;
  padding: 10px;
  margin: 10px;
  box-shadow: 5px 5px 8px #264653;
}

.noteItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 90%;
}

.noteName a {
    text-decoration: none;
    color: white;
}

.addNoteBtn {
  border: none;
  background: #ff7a59;
  color: #fff;
  padding: 10px;
  font-size: 16px;
  border-radius: 5px;
  position: relative;
  box-sizing: border-box;
  transition: all 500ms ease;
  margin-bottom: 20px;
}

.addNoteBtn:hover {
  color: #ff7a59;
  border: #ff7a59 solid 1px;
  background: #fff;
}

.deleteNoteBtn {
  border: none;
  background: #2a9d8f;
  color: #fff;
  padding: 5px;
  font-size: 16px;
  border-radius: 5px;
  position: relative;
  box-sizing: border-box;
  transition: all 500ms ease;
  margin: 15px 0px 10px 0px;
}

.deleteNoteBtn:hover {
  color: white;
  border: white solid 1px;
  background: red;
}
